<template>
    <div class="attr">
        <CommonAttr></CommonAttr>
        <el-form>
            <el-form-item label="">
                <el-switch
                    v-model="option.title.show"
                    active-text=""
                >
                </el-switch>
                <el-input
                    v-model="option.title.text"
                    placeholder=""
                >
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-switch
                    v-model="option.tooltip.show"
                    active-text=""
                >
                </el-switch>
            </el-form-item>
            <el-form-item label="">
                <el-switch
                    v-model="option.legend.show"
                    active-text=""
                >
                </el-switch>
            </el-form-item>
            <el-form-item label="">
                <el-switch
                    v-model="option.xAxis.show"
                    active-text=""
                >
                </el-switch>
            </el-form-item>
            <el-form-item>
                <el-dropdown>
                    <span class="el-dropdown-link">
                        <i class="el-icon-arrow-down el-icon--right"></i>
                    </span>
                    <el-dropdown-menu slot="dropdown">
                        <span v-for="(chart, index) in charts" :key="index" @click="selectchart(chart.title)">
                            <el-dropdown-item>{{ chart.name }}</el-dropdown-item>
                        </span>
                    </el-dropdown-menu>
                </el-dropdown>
            </el-form-item>
            <el-form-item label="">
                <el-button @click="openStaticWinbox"></el-button>
            </el-form-item>
        </el-form>

        <el-dialog
            title=""
            :visible.sync="dialogVisible"
            width="75%"
        >
            <div ref="ace" class="ace"></div>
            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="updatedata"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import ace from 'ace-builds'
import 'ace-builds/src-min-noconflict/theme-chrome'
import 'ace-builds/src-min-noconflict/mode-json5'
import CommonAttr from '@/custom-component/common/CommonAttr.vue'

export default {
    components: { CommonAttr },
    data() {
        return {
            dialogVisible: false,
            charts: [
                {
                    title: 'bar',
                    name: '',
                },
                {
                    title: 'scatter',
                    name: '',
                },
                {
                    title: 'line',
                    name: '',
                },
            ],
        }
    },
    computed: {
        curComponent() {
            return this.$store.state.curComponent
        },
        option() {
            return this.$store.state.curComponent.propValue.option
        },
    },
    methods: {
        openStaticWinbox() {
            this.dialogVisible = !this.dialogVisible
            this.$nextTick(() => {
                ace.config.set('basePath', 'https://cdnjs.cloudflare.com/ajax/libs/ace/1.4.14/')
                this.editor = ace.edit(this.$refs.ace, {
                    maxLines: 28,
                    minLines: 28,
                    fontSize: 14,
                    theme: 'ace/theme/chrome',
                    mode: 'ace/mode/json5',
                    tabSize: 4,
                    readOnly: false,
                    enableBasicAutocompletion: true,
                    enableLiveAutocompletion: true,
                    enableSnippets: true,
                })
                // editor
                let data = JSON.stringify(this.curComponent.propValue.option.series.data)
                let xAxis = JSON.stringify(this.curComponent.propValue.option.xAxis.data)
                this.editor.setValue(data + '\n' + xAxis)
            })
        },

        // []
        findstring(str, ch1, ch2) {
            return str.substr(str.indexOf(ch1), str.indexOf(ch2) + 1)
        },

        // editor
        updatedata() {
            let str = this.editor.getValue()
            let Arrdata = this.findstring(str, '[', ']')
            let ArrXAxis = this.findstring(str.substr(str.indexOf(']') + 1), '[', ']')
            this.curComponent.propValue.option.series.data = JSON.parse(Arrdata)
            this.curComponent.propValue.option.xAxis.data = JSON.parse(ArrXAxis)
            this.$message.success('')
            this.dialogVisible = !this.dialogVisible
        },

        // 
        selectchart(chart) {
            this.curComponent.propValue.option.series.type = chart
        },
    },
}
</script>

<style>
.ace {
    margin: 5px;
    margin-top: -20px;
    height: 600px;
    width: 100%;
}

.attr {
    margin: 13px;
}
</style>
